<template>
	<div id="select-goods">
		<common-header :title='title'></common-header>
		
		<div class="select-goods-list">
			<!-- 列表项 -->
			<div class="list-item">
				<div class="item-left">
					<img class="item-img" src="../../assets/pic.png"/>
				</div>
				<div class="item-right">
					<div class="item-name">酷美人 夜光石手链 男女款萤石手链手串手环饰品</div>
					<div class="item-btn">查看评价</div>
				</div>
			</div>
			<!-- 列表项 -->
			<div class="list-item">
				<div class="item-left">
					<img class="item-img" src="../../assets/pic.png"/>
				</div>
				<div class="item-right">
					<div class="item-name">酷美人 夜光石手链 男女款萤石手链手串手环饰品</div>
					<div class="item-btn active" @click="toPublish">立即评价</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
import CommonHeader from '@/components/Header/CommonHeader'

export default {
	data(){
		return {
			title:"选择商品",
			
		}
	},
  components: {
  	CommonHeader
  },
  methods:{
  	toPublish(){
  		this.$router.push({name:'Publish'});
  	},
  }
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#select-goods{
		height: 100%;
		background: #f0f0f0;
		.select-goods-list{
			background: #fff;
		}
		.list-item{
			display: flex;
			padding: 0.27rem 0.4rem;
			border-bottom: 1px solid $border-color;
			.item-left{
					border:1px solid $border-color;
					border-radius: 0.11rem;
					margin-right: 0.33rem;
				.item-img{
					width: 2.13rem;
					height: 2.13rem;

				}
				
			}
			.item-right{
				font-size: 0.37rem;
				flex: 1;
				.item-name{
					color: #1a1a1a;
					@include textflow-two;
				}
				.item-btn{
					float: right;
					width: 2.0rem;
					height: 0.75rem;
					line-height: 0.72rem;
					margin-top: 0.45rem;
					text-align: center;
					border: 1px solid $border-color;
					border-radius: 0.37rem;
					color: #333;
				}
				.active{
					color: $label-color;
					border-color: $label-color;
				}
			}
		}

	}
</style>


